class Child extends React.Component{
  getFVal=()=>{
    console.log('content',this.props.content)
    this.props.sendFun('true')
  }
  render(){
    return  (
      <div>
          <h2></h2>
          <p>{this.props.content}</p>
          <button onClick={this.getFVal}></button>
      </div>
    )
  }
}
export default class ReactKnowledgeDemo3 extends React.Component {
    sendFun=(sonVal)=>{
        console.log('',sonVal)
    }
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false,
            height:`100%`         
          },
          markContent: content
        }
        return (
            <div>              
                <div style={{height:'467px'}}>
                   <h2 className="h2_common">React--props</h2>
                   <MarkDown {...markConfig}/>
                </div>
                <Child content='false' sendFun={this.sendFun}></Child>
            </div>
        )
    }
}
