import React from "react";
import "./Wirkung.css";
//  
import { Switch, Redirect, Link, useHistory } from "react-router-dom";
import NestedRoute from '@service/router/NestedRoute'
// import { renderRoutes } from "react-router-config";
export default class Wirkung extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      linkList: [
        {
          path: "/wirkung",
          name: ""
        },
        {
          path: "/wirkung/to_reactknowledge",
          name: "React",
          isShow: false,
          children:[
            { path: "/wirkung/to_reactknowledge/to_reactknowledge_demo4", name: "React-constructorsuper" },
            { path: "/wirkung/to_reactknowledge/to_reactknowledge_demo1", name: "React-" },
            { path: "/wirkung/to_reactknowledge/to_reactknowledge_demo2", name: "React-state" },
            { path: "/wirkung/to_reactknowledge/to_reactknowledge_demo3", name: "React--props" },
            { path: "/wirkung/to_reactknowledge/to_reactknowledge_demo5", name: "React--Context" },
            { path: "/wirkung/to_reactknowledge/to_reactknowledge_demo6", name: "React-" },
          ]
        },
        {
          path: "/wirkung/to_react_redux",
          name: "Redux",
          isShow:false,
          children:[
            { path: "/wirkung/to_react_redux/to_react_redux_demo1", name: "Redux-" },
            { path: "/wirkung/to_react_redux/to_react_redux_demo2", name: "Redux-" },
          ]
        },
        {
          path: "/wirkung/to_react_router",
          name: "Router",
          isShow:false,
          children:[
            { path: "/wirkung/to_react_router/to_react_router_demo1", name: "Router-" },
          ]
        },
        {
          path: "/wirkung/to_markdown_edit",
          name: "markdown"
        },
        {
          path: "/wirkung/to_iconfont",
          name: "iconfont"
        },
        {
          path: "/wirkung/to_font",
          name: "font"
        },
        {
          path: "/wirkung/to_classnames",
          name: "classnames"
        },
        {
          path: "/wirkung/to_brafteditor",
          name: "BraftEditor",
          isShow: false,
          children: [
            { path: "/wirkung/to_brafteditor/to_brafteditor_demo1", name: "BraftEditor-" },
            { path: "/wirkung/to_brafteditor/to_brafteditor_demo2", name: "BraftEditor-" }
          ]
        }
      ]
    };
    this.useHistoryClick = this.useHistoryClick.bind(this);
  }
  useHistoryClick() {
    // 
    let history = useHistory();
    console.log("history", history);
    // history.push('/')
  }
  // 
  linkMouseEnter = (item, bol, e) => {
    if (item.children) {
      item.isShow = bol;
      // console.log(this.state)
      this.setState({
        linkList: this.state.linkList
      });
    }
  };
  render() {
    // console.log('Wirkung this.props',this.props)
    const { location, router } = this.props;
    return (
      <div className="Wirkung App_main">
        {/* <Prompt message="Are you sure you want to leave?" /> */}
        <div className="common_header">
          <Link
            className="App-link"
            to="/document"
            style={{ position: "absolute", left: 0, top: "10px" }}
          >
            {" "}
            &lt;--
          </Link>
          {/* <a className="App-link" href="#/" target="_self" rel="noopener noreferrer">></a> */}
          <div className="common_title">{this.state.title}</div>
          <hr />
        </div>
        <div className="common_content">
          <div className="Router_example_1 common_box">
            {/* (event)=>this.linkMouseEnter(item,event) */}
            {/* this.linkMouseEnter.bind(this,item) */}
            {this.state.linkList.map(item => (
              <div
                className="link_item"
                key={item.path}                
              >
                <Link
                  className="App-link drop_title"
                  onMouseEnter={event => this.linkMouseEnter(item, true, event)}
                  onMouseLeave={event => this.linkMouseEnter(item, false, event)}
                  to={item.path}
                  style={{ display: "inline-block" }}
                >
                  <div className="title">{item.name}</div>
                </Link>
                {item.children && item.isShow && (
                  <div className="dropmenu" 
                  onMouseEnter={event => this.linkMouseEnter(item, true, event)}
                  onMouseLeave={event => this.linkMouseEnter(item, false, event)}>
                    <ul>
                      {item.children.map(cItem => (
                        <li className="dropmenu_item" key={cItem.path}>
                          <Link to={{pathname:cItem.path,state:{name:cItem.name}}}>{cItem.name}</Link>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            ))}
          </div>
          <div className="common_box sureHeight">
            {location.pathname === "/wirkung"
              ? defaultComponent()
              : <Switch>
                   {/* {renderRoutes(router)} */}
                   {router.map((route, i) => (
                    <NestedRoute {...route} key={i} />
                  ))}
                   <Redirect to="/wirkung" />
                </Switch>}
          </div>
        </div>
      </div>
    );
  }
}

const defaultComponent = () => {
  return (
    <div className="banner">
      <div className="name_say">
        <span></span>
        <span className="line"></span>
        <img className="img" src="/img/tiandao.jfif" alt="" />
      </div>
    </div>
  );
};
