// react
import React from "react";
import { Switch, Redirect } from "react-router-dom";
import NestedRoute from "@service/router/NestedRoute";
// import { renderRoutes } from 'react-router-config' //

export default class ReactRouter extends React.Component {
  render() {
    const { location, router } = this.props;

    return location.pathname === "/wirkung/to_react_router" ? (
      <div>
        <h2>Redux</h2>
      </div>
    ) : (
      <Switch>
        {/* {renderRoutes(router)} */}
        {router.map((route, i) => (
          <NestedRoute {...route} key={i} />
        ))}
        <Redirect to="/wirkung/to_react_router" />
      </Switch>
    );
  }
}
