// 
import React from 'react';

import { Link } from 'react-router-dom'

// props.children 
function FancyBorder(props) {
  return (
    <div className={'FancyBorder FancyBorder-' + props.color}>
      {props.children}
    </div>
  );
}

// slot
function SplitPane(props) {
  console.log('',props)
  return (
    <div className="SplitPane">
      <div className="SplitPane-left">
        {props.left}
      </div>
      <div className="SplitPane-right">
        {props.right}
      </div>
      {props.children}{props.children.props.className}
    </div>
  );
}
// props.children 
export default class PropsChildren extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:'props-children'
      }
    }
    render() {
        return (
            <div className="Template App_main">
                <div className="common_header">
                  <Link
                    className="App-link"
                    to="/react-basic"
                    style={{ position: "absolute", left: 0, top: "10px" }}
                  >
                    {" "}
                    &lt;--
                  </Link>
                   <div className="common_title">{this.state.title}</div>
                </div>
                <div className="common_content">
                     <div style={{border:'1px solid #c4c4c4',paddingBottom:'20px',minHeight:'300px'}}>
                            <FancyBorder color="blue">
                              <h1 className="Dialog-title">
                                Welcome
                              </h1>
                              <p className="Dialog-message">
                                Thank you for visiting our spacecraft!
                              </p>
                            </FancyBorder>
                            <SplitPane left={'I am left'} right={'I am right'}>
                               <div className="SplitPane-children">
                                  I am children
                               </div>
                            </SplitPane>
                            <div className="common_code_style" style={{whiteSpace:'pre'}}>
                            {`
props.children code:
    <FancyBorder color="blue">
        <h1 className="Dialog-title">
          Welcome
        </h1>
        <p className="Dialog-message">
          Thank you for visiting our spacecraft!
        </p>
    </FancyBorder>
    function FancyBorder(props) {
      return (
        <div className={'FancyBorder FancyBorder-' + props.color}>
          {props.children}
        </div>
       );
    }
                                
                            `}
                            <div className="common_code_style" style={{whiteSpace:'pre'}}>
                            {`
props code:
    <SplitPane left={'I am left'} right={'I am right'}>
        <div className="SplitPane-children">
          I am children
        </div>
    </SplitPane>
    function SplitPane(props) {
      console.log('',props)
      return (
        <div className="SplitPane">
          <div className="SplitPane-left">
            {props.left}
          </div>
          <div className="SplitPane-right">
            {props.right}
          </div>
          {props.children}{props.children.props.className}
        </div>
      );
    }
                                
                            `}
                            </div>
                         </div>
                     </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
    }
}