// ReactKnowledgeDemo1
import React from 'react';
import MarkDown from '@/views/Wirkung/components/markdown/markdown-edit' 
const content=`
\`\`\` js
//
 (event)=>this.linkMouseEnter(item,event) 
 this.linkMouseEnter.bind(this,item) 

//
// renderevent
this.state.arrList,map(item=>(<div onClick={(event)=>this.linkMouseEnter(item,event) }></div>))
this.state.arrList,map(item=>(<div onClick={ this.linkMouseEnter.bind(this,item) }></div>))

// 
linkMouseEnter=(item,e)=>{}
\`\`\`
`
export default class ReactKnowledgeDemo1 extends React.Component {
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false       
          },
          markContent: content
        }
        return (
            <div>
                <h2 className="h2_common">React-</h2>
                <MarkDown {...markConfig}/>
            </div>
        )
    }
}