// react
import React from "react";
import { Switch, Redirect } from "react-router-dom";
import NestedRoute from "@service/router/NestedRoute";
// import { renderRoutes } from 'react-router-config' //
import "./index.scss";

export default class ReactKnowledge extends React.Component {
  render() {
    console.log("ReactKnowledge this.props", this.props);
    const { location, router } = this.props;
    return location.pathname === "/wirkung/to_reactknowledge" ? (
      <div>
        <h2>React Google Grid Gallery</h2>
      </div>
    ) : (
      <Switch>
        {/* {renderRoutes(router)} */}
        {router.map((route, i) => (
          <NestedRoute {...route} key={i} />
        ))}
        <Redirect to="/wirkung/to_reactknowledge" />
      </Switch>
    );
  }
}
