import React from "react";
import {  Link  } from "react-router-dom";
import { connect } from 'react-redux'
import "./document.css";
// 
class Document extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      list: [
        {
          name: "react",
          path: "/react-basic"
        },
        {
          name: "",
          path: "/router"
        },        
        {
          name: "",
          path: "/wirkung"
        }
      ]
    };
  }
  render() {    
    return (
      <div className="Document App_main">
        <div className="common_header">
          <Link
            className="App-link"
            to="/"
            style={{ position: "absolute", left: 0, top: "10px" }}
          >
            {" "}
            &lt;--
          </Link>
          {/* <a className="App-link" href="#/" target="_self" rel="noopener noreferrer">></a> */}
          <div className="common_title">{this.state.title}</div>
          <hr />
        </div>
        <div className="common_content">
          <ul>
            {this.state.list.map((v,index) => (
              <li key={index}>
                <Link className="App-link" to={v.path}>
                  {index+1} {v.name}
                </Link>
              </li>
            ))}
          </ul>
        </div>      
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  // console.log('state',state) //redux store
  return {
    userInfo: state.userInfo
  }
}

export default connect(mapStateToProps)(Document)