// BraftEditor
import React from "react";
import { Switch, Redirect } from "react-router-dom";
import NestedRoute from "@service/router/NestedRoute";
import "braft-editor/dist/index.css";
import BraftEditor from "braft-editor"; //BraftEditor
// import { renderRoutes } from 'react-router-config' //
// const content=`
// \`\`\` txt
// 1.
// 2.assets
// 3.import './assets/iconfonts/iconfont.css'
// 4.<span className="iconfont icon-gouwucheman"></span>
// \`\`\`
// `
// 1 https://www.yuque.com/braft-editor/be/gz44tn#bo49ph
// 2 https://braft.margox.cn/demos/custom
export default class PBrafteditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      editorState: BraftEditor.createEditorState("<p>Hello <b>World!</b></p>") // 
    };
  }
  handleChange = () => {};
  render() {
    const { location, router } = this.props;
    const { editorState } = this.state;
    return (
      <div>
        {location.pathname === "/wirkung/to_brafteditor" ? (
          <div>
            <h2>BraftEditor</h2>
            <BraftEditor value={editorState} onChange={this.handleChange} />
          </div>
        ) : (
          <Switch>
            {/* {renderRoutes(router)} */}
            {router.map((route, i) => (
              <NestedRoute {...route} key={i} />
            ))}
            <Redirect to="/wirkung/to_brafteditor" />
          </Switch>
        )}
      </div>
    );
  }
}
