const options = {
  includeEditors: ["editor-with-code-highlighter"], // BraftEditorBraftEditor
  syntaxs: [
    {
      name: "JavaScript",
      syntax: "javascript"
    },
    {
      name: "HTML",
      syntax: "html"
    },
    {
      name: "CSS",
      syntax: "css"
    },
    {
      name: "Java",
      syntax: "java"
    },
    {
      name: "PHP",
      syntax: "php"
    }
  ]
};
BraftEditor.use(CodeHighlighter(options));
