export default class BrafteditorDemo1 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      editorState: BraftEditor.createEditorState(code) // 
    };
  }
  handleChange = () => {};
  render() {
    // const { editorState } = this.state;
    const controls = [
      {
        key: "bold",
        text: <b></b>
      },
      "italic",
      "underline",
      "separator",
      "link",
      "separator",
      "code"
    ];
    return (
      <div>
        <h2>demo1-</h2>
        <BraftEditor
          id="editor-with-code-highlighter"
          controls={controls}
          // value={editorState}
          onChange={this.handleChange}
        />
      </div>
    );
  }
}
