import MarkdownEdit from "../../views/Wirkung/view/p_markdown"; //markdown
import Iconfont from "../../views/Wirkung/view/p_iconfont"; //iconfont
import Font from "../../views/Wirkung/view/p_font"; //font
import Classnames from "../../views/Wirkung/view/p_classnames"; //classnames,class
import BraftEditor from "../../views/Wirkung/view/p_BraftEditor"; //BraftEditor
