import React from 'react';
import { Switch, Redirect  } from "react-router-dom";
import { connect } from 'react-redux'
import { router } from '@service/router/index1'
import NestedRoute from '@service/router/NestedRoute'

class Home extends React.Component {
    render() {
        let { userInfo ,history } = this.props    
        if(Object.keys(userInfo).length===0){
          history.push('/login') 
          return null
        }
        return (
            <div className="doucment">
                <Switch>
                  {router.map((route, i) => (
                    <NestedRoute {...route} key={i} />
                  ))}
                  <Redirect to="/document" />
                </Switch>
            </div>
        )
    }
}
const mapStateToProps = (state) => {
  // console.log('state',state) //redux store
  return {
    userInfo: state.userInfo
  }
}

export default connect(mapStateToProps)(Home)