                    <div id="title1">
                      <h3
                        className="title_id"
                        onClick={this.handleScrollTop.bind(this)}
                        style={{ padding: "10px 30px", margin: "0" }}
                      >
                        
                      </h3>
                      <div className="common_code_style">
                        {"//"}1: (, )
                        <div>
                          {
                            "function MyComponent1() { return <h1>11111</h1>; }"
                          }
                        </div>
                        {"//"}2: ES6(, )
                        <div>
                          {
                            "class MyComponent3 extends React.Component {render () { return <h1>33333</h1>; }}"
                          }
                        </div>
                        {"//"}3: ES5()
                        <div>
                          {
                            " var MyComponent2 = React.createClass({render () { return <h1>22222</h1>; } });"
                          }
                        </div>
                      </div>
                    </div>
                    {/* 8---------------------------------------------------- */}
                    <div id="title2">
                      <h3
                        className="title_id"
                        onClick={this.handleScrollTop.bind(this)}
                        style={{ padding: "10px 30px", margin: "0" }}
                      >
                        react
                      </h3>
                      <div className="common_code_style">
                        {"//"}
                        <div>
                          constructor() <br />
                          componentWillMount() : 
                          <br />
                          render() : DOM
                          <br />
                          componentDidMount() : 
                          <br />
                        </div>
                        {"//"}state
                        <div>
                          componentWillReceiveProps(): 
                          <br />
                          componentWillUpdate() : 
                          <br />
                          render() : ()
                          <br />
                          componentDidUpdate() : 
                          <br />
                        </div>
                        {"//"}
                        <div>
                          ReactDOM.unmountComponentAtNode(document.getElementById('example'))
                          : 
                          <br />
                          componentWillUnmount() : 
                          <br />
                        </div>
                      </div>
                    </div>
                    {/* 9---------------------------------------------------- */}
                    <div id="title3">
                      <h3
                        className="title_id"
                        onClick={this.handleScrollTop.bind(this)}
                        style={{ padding: "10px 30px", margin: "0" }}
                      >
                        
                      </h3>
                      {/* <div>{['cc ', <span>&copy;</span>, ' 2015']}</div> */}
                      <div
                        dangerouslySetInnerHTML={{ __html: "cc &copy; 2015" }}
                      />
                    </div>
                    {/* 10---------------------------------------------------- */}
                    <div id="title4">
                      <h3
                        className="title_id"
                        onClick={this.handleScrollTop.bind(this)}
                        style={{ padding: "10px 30px", margin: "0" }}
                      >
                        JSX
                      </h3>
                      <div className="common_code_style">
                        {"//"} JSX
                        <br />
                        {'<div className="navigation">Hello World!</div>'}
                        <br />
                        {"//"} React API
                        <br />
                        {` React.createElement('div', {className: 'navigation'}, [
                        React.createElement('span',{className:'a'},'a'),
                        React.createElement('span',{className:'b'},'b')
                    ])`}
                      </div>
                    </div>
                    {/* 11---------------------------------------------------- */}
                    <div id="title5">
                      <h3
                        className="title_id"
                        onClick={this.handleScrollTop.bind(this)}
                        style={{ padding: "10px 30px", margin: "0" }}
                      >
                        refdom
                      </h3>
                      <div className="common_code_style">
                        <input
                          tyoe="input"
                          ref="ref1"
                          value={this.state.refValue1}
                          onChange={this.refClick1.bind(this)}
                        />
                        <button
                          className="btn"
                          onClick={this.refClick2.bind(this)}
                        >
                          
                        </button>
                      </div>
                    </div>
                    {/* 12---------------------------------------------------- */}
                    <div id="title6">
                      <h3
                        className="title_id"
                        onClick={this.handleScrollTop.bind(this)}
                        style={{ padding: "10px 30px", margin: "0" }}
                      >
                        (propTypes)
                      </h3>
                      <div className="common_code_style">
                        <Link className="App-link" to="/proptypes">
                          propTypes
                        </Link>
                      </div>
                    </div>
