import React from 'react';

// import { Link } from 'react-router-dom'
export default class ChildLabel extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:'B',
        value:''
      }
    }
    render() {
        return (
            <div className="ChildLabel">
                  {this.state.title}
                  <div>
                     B 1. {this.props.searchValue}  2. {this.props.searchValue2}
                  </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
    }
}