import React from 'react';

import { Link } from 'react-router-dom'
import PropTypes from 'prop-types' //

export default class PropType extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:'(propTypes)',
      }
    }
    render() {
        return (
            <div className="PropType App_main">
                <div className="common_header">
                  <Link
                    className="App-link"
                    to="/react-basic"
                    style={{ position: "absolute", left: 0, top: "10px" }}
                  >
                    {" "}
                    &lt;--
                  </Link>
                   <div className="common_title">{this.state.title}</div>
                </div>
                <div className="common_content">
                     <div style={{border:'1px solid #c4c4c4',paddingBottom:'20px',minHeight:'300px'}}>
                          <div className="common_code_style" style={{whiteSpace:'pre'}}>
                              {`  
                                  // npm install prop-types --save 
                                  import React from 'react';
                                  import ReactDOM from 'react-dom';
                                  import PropTypes from 'prop-types'

                                  class Component1 extends React.Component{
                                      render(){
                                          return (
                                              <div>
                                                  <p>{this.props.name}</p>
                                                  <p>{this.props.age}</p>
                                                  <p></p>
                                                  <ul>
                                                  {
                                                      this.props.subjects.map(function(_item){
                                                          return <li>{_item}</li>
                                                      })
                                                  }
                                                  </ul>
                                              </div>
                                          )
                                      }
                                  }

                                  // name 
                                  Component1.propTypes = {
                                      name: PropTypes.string
                                  }

                                  ReactDOM.render(<Component1 name="Tom"/>, document.getElementById('div1'));

                                  prop 
                                  PropTypes.array
                                  PropTypes.bool
                                  PropTypes.func
                                  PropTypes.number
                                  PropTypes.object
                                  PropTypes.string
                                  PropTypes.symbol
                                  PropTypes.any.isRequired
                              
                              `}
                          </div> 
                     </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
    }
}
// 
PropType.propTypes = {
  name: PropTypes.string
}