// 
import React from 'react';
import MarkDown from '../../components/markdown/markdown-edit'
const content=`
\`\`\` txt
1.
2.assets
3.import './assets/iconfonts/iconfont.css'
4.<span className="iconfont icon-gouwucheman"></span>
\`\`\`
`
export default class PIconfont extends React.Component {
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false,
            height:`100%`           
          },
          markContent:content
        }
        return (
            <div>
                <h2>iconfont<span className="iconfont icon-gouwucheman"></span></h2>
                <div>
                   <MarkDown {...markConfig}/>
                </div>
            </div>
        )
    }
}