import React from 'react';
import PropTypes from 'prop-types'

import { Link } from 'react-router-dom'
import ThemedButton from '../child2/theme-button'
import ThemedButton1 from '../child2/theme-button1'
// const ThemeContext = React.createContext('light');
function Toolbar(props) {
  return (
    <div>
      <ThemedButton />
      <ThemedButton1/>
    </div>
  );
}

export default class Context extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:''
      }
    }
    static childContextTypes = {
      propA: PropTypes.string,
      methodA: PropTypes.func
    }
    getChildContext(){
      return {
        propA: 'propA',
        methodA: () => 'methodA'
      }
    }
    render() {
        return (
            <div className="Template App_main">
                <div className="common_header">
                  <Link
                    className="App-link"
                    to="/react-basic"
                    style={{ position: "absolute", left: 0, top: "10px" }}
                  >
                    {" "}
                    &lt;--
                  </Link>
                   <div className="common_title">{this.state.title}</div>
                </div>
                <div className="common_content">
                     <div style={{border:'1px solid #c4c4c4',paddingBottom:'20px',minHeight:'300px'}}>
                            <Toolbar />
                     </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
    }
}

