// 
import React from 'react';

import { Link } from 'react-router-dom'
import ChildComponent from '../child2/child-component'
import ChildSearch from '../child2/child-search'
import ChildLabel from '../child2/child-label'
export default class PassByValue extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:'',
        sonInputValue:'123',
        brotherValue:'123',
        searchValue:'123456',
        searchValue2:''
      }
    }
    // 
    getSonInfo(){
      console.log(this.refs.ChildComponent)
      this.refs.ChildComponent.state.value=''
    }
    // 
    changeInputValue(value){
      console.log(value) //
      this.setState({
        sonInputValue:value
      })
    }

    // 
    searchChange(searchValue){
      console.log('A',searchValue)
      this.setState({
        searchValue
      })
    } 
    // 2
    searchChange2(searchValue2){
      this.setState({
        searchValue2
      })
    }
    render() {
        return (
            <div className="PassByValue App_main">
                <div className="common_header">
                  <Link
                    className="App-link"
                    to="/react-basic"
                    style={{ position: "absolute", left: 0, top: "10px" }}
                  >
                    {" "}
                    &lt;--
                  </Link>
                   <div className="common_title">{this.state.title}</div>
                </div>
                <div className="common_content">                     
                     <div style={{border:'1px solid #c4c4c4',paddingBottom:'20px',minHeight:'300px'}}>
                          <div>
                              <input type='button' value={''} onClick={this.getSonInfo.bind(this)}/>
                          </div>
                          <br/>
                          <ChildComponent ref="ChildComponent" fatherInfo={'-'} sonInputValue={this.state.sonInputValue} changeInputValue={this.changeInputValue.bind(this)}/>
                          <br/>
                          <div></div>
                            <ChildSearch ref="ChildSearch" searchValue={this.state.searchValue}  searchChange={this.searchChange.bind(this)} searchChange2={this.searchChange2.bind(this)}/>
                            <ChildLabel ref="ChildLabel" searchValue={this.state.searchValue} searchValue2={this.state.searchValue2}/>
                          <br/>
                          <br/>
                          <h3></h3>
                          <div style={{whiteSpace:'pre'}}>
                               <br/>
                              {`<ChildComponent  fatherInfo={'-'} />`}<br/>
                               fatherInfo, super(props)props this.props.fatherInfo<br/><br/>
                              <br/>
                              {`<ChildComponent  fatherInputChange={this.fatherInputChange.bind(this)} />`}<br/>
                                fatherInputChange, super(props)props this.props.fatherInputChange(1,2,3)<br/>
                              fatherInputChange, this.setState<br/><br/>
                              
                              {`<ChildComponent ref='ChildComponent' /> `}<br/>
                               this.refs.ChildComponent<br/><br/>
                              <br/>
                              props this.props.searchChange(e.target.value) setState

                          </div>
                     </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
      console.log('searchA',this.refs.ChildSearch.state.searchA)
      this.setState({
        searchValue2:this.refs.ChildSearch.state.searchA
      })
    }
    componentWillUnmount() {
      // 
      this.setState = (state, callback) => {
        return
      }
    }
}