import React from "react";
// import MdEditor from "react-markdown-editor-lite";
import MdEditor from '@/components/markdown/index.js'
import MarkdownIt from 'markdown-it'
import emoji from 'markdown-it-emoji'
import subscript from 'markdown-it-sub'
import superscript from 'markdown-it-sup'
import footnote from 'markdown-it-footnote'
import deflist from 'markdown-it-deflist'
import abbreviation from 'markdown-it-abbr'
import insert from 'markdown-it-ins'
import mark from 'markdown-it-mark'
import tasklists from 'markdown-it-task-lists'
import hljs from 'highlight.js'
// import 'highlight.js/styles/atom-one-light.css'
// import 'highlight.js/styles/atelier-seaside-dark.css'
import 'highlight.js/styles/atelier-cave-dark.css'
// import 'highlight.js/styles/github.css'
import './markdown-edit.css'
import content from './content'
var cs= require('classnames');
// const mock_content = content;

hljs.registerLanguage('css',require('highlight.js/lib/languages/css'));
hljs.registerLanguage('json',require('highlight.js/lib/languages/json'));
hljs.registerLanguage('less',require('highlight.js/lib/languages/less'));
export default class MarkdownEdit extends React.Component {
  mdEditor = null;
  static defaultProps={
    defaultContent: content,
    defaultConfig:{
      height:330,
      preview:false,
      menu:true,//
      md:true,//md
      html:true,//html
      toolbar_show:true,//toolbar
    }
  }
  static getDerivedStateFromProps(props,state){
    return {
      markContent:props.markContent,
      markConfig:{...props.defaultConfig,...(props.markConfig||{})}
    }

  }
  constructor(props) {
    super(props);
    this.state = {
      markContent:'',
      markConfig:{}
    };

    this.mdParser = new MarkdownIt({
      html: true,
      linkify: true,
      typographer: true,
      highlight: function (str, lang) {
        if (lang && hljs.getLanguage(lang)) {
          try {
            return hljs.highlight(lang, str).value
          } catch (__) {}
        }    
        return '' // use external default escaping
      }
      // highlight:function (str, lang) {       
      //   if (lang && hljs.getLanguage(lang)) {
      //     try {
      //       return '<pre class="hljs"><code>' +
      //              hljs.highlight(lang, str, true).value +
      //              '</code></pre>';
      //     } catch (__) {}
      //   }
       
      //   return '';
      // }
    })
    .use(emoji)
    .use(subscript)
    .use(superscript)
    .use(footnote)
    .use(deflist)
    .use(abbreviation)
    .use(insert)
    .use(mark)
    .use(tasklists, { enabled: this.taskLists })
  }
  handleEditorChange=({html, md}) =>{
    console.log('handleEditorChange', html, md)
  }
  handleImageUpload=(file, callback)=> {
    const reader = new FileReader()
    reader.onload = () => {      
      // const convertBase64UrlToBlob = (urlData) => {  
      //   let arr = urlData.split(','), mime = arr[0].match(/:(.*?);/)[1]
      //   let bstr = atob(arr[1])
      //   let n = bstr.length
      //   let u8arr = new Uint8Array(n)
      //   while (n--) {
      //     u8arr[n] = bstr.charCodeAt(n)
      //   }
      //   return new Blob([u8arr], {type:mime})
      // }
      // const blob = convertBase64UrlToBlob(reader.result)
      // setTimeout(() => {
      //   // setTimeout 
      //   // calbackimageUrlmarkdown
      //   callback('https://avatars0.githubusercontent.com/u/21263805?s=40&v=4')
      // }, 1000)
    }
    reader.readAsDataURL(file)
  }
  onBeforeClear = () => {
    return new Promise((resolve, reject) => {
      const result = window.confirm('Are you sure you want to clear your markdown :-)')
      const toClear = result ? true : false
      resolve(toClear)
      // custom confirm message pseudo code
      // YourCustomDialog.open(() => {
      //   // confirm callback
      //   resolve(true)
      // }, () => {
      //   // cancel callback
      //   resolve(false)
      // })
    })
  }
  // html
  renderHTML=(text)=> {
    // Markdown
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve(this.mdParser.render(text))
      }, 200)
    })
  }
  handleGetMdValue = () => {   
    this.mdEditor && alert(this.mdEditor.getMdValue())      
  }
  handleGetHtmlValue = () => {    
    this.mdEditor && alert(this.mdEditor.getHtmlValue())
  }  
  // props
  // UNSAFE_componentWillReceiveProps(nextProps,nextContext){
  //   console.log(`this.props-->`,this.props);
  //   console.log(`nextProps-->`,nextProps);
  //   console.log(`nextContext-->`,nextContext);
  //   this.setState({
  //     markContent:this.props.markContent,
  //     markConfig:{...this.props.defaultConfig,...(this.props.markConfig||{})}
  //   })
  // }
  render() {
    return (
      <div className="markdown-edit" style={{height: `calc(100% - 28px)`}}>
        {/* <nav>
          <button onClick={this.handleGetMdValue} >getMdValue</button>  
          <button onClick={this.handleGetHtmlValue} >getHtmlValue</button>  
        </nav> */}
        <div className={cs("code-box",{"preview":this.state.markConfig.preview})}  style={{height:typeof this.state.markConfig.height === 'number'?(this.state.markConfig.height+'px'):this.state.markConfig.height}}>
          <MdEditor
            ref={node => (this.mdEditor = node)}
            value={this.state.markContent}
            style={{}}
            renderHTML={this.renderHTML}
            config={{
              view: {
                menu: this.state.markConfig.menu,
                md: this.state.markConfig.md,
                html: this.state.markConfig.html,
                toolbar_show:this.state.markConfig.toolbar_show
              },
              // imageUrl: "https://octodex.github.com/images/minion.png",
              clearTip: 'Are you sure you want to clear your markdown ???'
            }}            
            onChange={this.handleEditorChange} //
            onImageUpload={this.handleImageUpload} //
            onBeforeClear={this.onBeforeClear} //
          />
        </div>
      </div>
    );
  }
}


