import React from "react";
 
import {
  // HashRouter as Router,
  Route,Link
} from 'react-router-dom'
import Router1_1 from '../router1_child/router1_1'
import Router1_2 from '../router1_child/router1_2'
export default class Router1 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title:'1'
    };
  }
  render() {
    return (
          <div className="Router App_main">
            <div className="common_header">
              <Link
                className="App-link"
                to="/router"
                style={{ position: "absolute", left: 0, top: "10px" }}
              >
                {" "}
                &lt;--
              </Link>
              {/* <a className="App-link" href="#/" target="_self" rel="noopener noreferrer">></a> */}
              <div className="common_title">{this.state.title}</div>
              <hr />
            </div>
            <div className="common_content">
                <h2 style={{display:'inline-block',margin:'10px 20px',color:'#eee',borderBottom:'1px solid #eee'}}>react-router-dom </h2>
                <div className="Router_example_1 common_box">
                    <Link className="App-link" to="/router1/router1_1" style={{margin:'10px 0 0 10px', display: 'inline-block'}}>
                        router1_1
                    </Link>
                    <Link className="App-link" to="/router1/router1_2" style={{margin:'10px 0 0 10px', display: 'inline-block'}}>
                        router1_2
                    </Link>
                </div>
            </div>
             <div>
                <Route path="/router1/router1_1/"   component={Router1_1} />
                <Route path="/router1/router1_2/"   component={Router1_2} />
             </div>
          </div>
    );
  }
}
