// BraftEditor-demo2
import "braft-editor/dist/index.css";
import "braft-extensions/dist/code-highlighter.css";
import React from "react";
import "braft-editor/dist/index.css";
import BraftEditor from "braft-editor"; //BraftEditor
import CodeHighlighter from "braft-extensions/dist/code-highlighter";
import 'prismjs/components/prism-java'
import 'prismjs/components/prism-php'
// 1 https://www.yuque.com/braft-editor/be/gz44tn#bo49ph
// 2 https://braft.margox.cn/demos/custom

export default class BrafteditorDemo2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      editorState: null // 
    };
  }
  handleChange = (editorState) => {
    this.setState({ editorState })
  }
  buildPreviewHtml() {
    return `
        <!Doctype html>
        <html>
          <head>
            <title>Preview Content</title>
            <style>
              html,body{
                height: 100%;
                margin: 0;
                padding: 0;
                overflow: auto;
                background-color: #f1f2f3;
              }
              .container{
                box-sizing: border-box;
                width: 1000px;
                max-width: 100%;
                min-height: 100%;
                margin: 0 auto;
                padding: 30px 20px;
                overflow: hidden;
                background-color: #fff;
                border-right: solid 1px #eee;
                border-left: solid 1px #eee;
              }
              .container img,
              .container audio,
              .container video{
                max-width: 100%;
                height: auto;
              }
              .container p{
                white-space: pre-wrap;
                min-height: 1em;
              }
              .container pre{
                padding: 15px;
                background-color: #f1f1f1;
                border-radius: 5px;
              }
              .container blockquote{
                margin: 0;
                padding: 15px;
                background-color: #f1f1f1;
                border-left: 3px solid #d1d1d1;
              }
            </style>
          </head>
          <body>
            <div class="container">${this.state.editorState.toHTML()}</div>
          </body>
        </html>
      `;
  }
  preview = () => {
    if (window.previewWindow) {
      window.previewWindow.close()
    }

    window.previewWindow = window.open()
    window.previewWindow.document.write(this.buildPreviewHtml())
    window.previewWindow.document.close()

  }
  componentDidMount(){
    // this.setState({
    //    editorState: BraftEditor.createEditorState(`helloworld`)
    // })
  }
  render() {
    // const { editorState } = this.state;
    const extendControls = [
      {
        key: "custom-button",
        type: "button",
        text: "",
        onClick: this.preview
      }
    ];
    const controls = [
      {
        key: "bold",
        text: <b></b>
      },
      "italic",
      "underline",
      "separator",
      "link",
      "separator",
      "code"
    ];
    const options = {
      includeEditors: ["editor-with-code-highlighter"], // BraftEditorBraftEditor
      syntaxs: [
        {
          name: "JavaScript",
          syntax: "javascript"
        },
        {
          name: "HTML",
          syntax: "html"
        },
        {
          name: "CSS",
          syntax: "css"
        },
        {
          name: "Java",
          syntax: "java"
        },
        {
          name: "PHP",
          syntax: "php"
        }
      ]
    };
    BraftEditor.use(CodeHighlighter(options));
    return (      
      <div>
        <h2>demo2-</h2>
        <BraftEditor
          id="editor-with-code-highlighter"
          controls={controls}
          extendControls={extendControls}
          value={this.state.editorState}
          onChange={this.handleChange}
        />
      </div>
    );
  }
}
