// import { AnyAction, combineReducers } from "redux"
import { combineReducers } from "redux"
import * as ActionTypes from './action-types' //

// 
let initialState = {
  addressList: [],   // 
  addressName: '',  // 
  temMessage: '', //
  hasAddressList: [], // 
  operate: 'edit',
  geohash: [],
  banners:[],
  userInfo:JSON.parse(localStorage.getItem('USER'))||{},//
}

function combineBanners( banners= initialState.banners , action={} ){
  switch (action.type) {
    case ActionTypes.SET_BANNERS:
      return action.banners;
    default:
      return banners;
  }
}
function combineUserInfo (userInfo= initialState.userInfo , action={}){
  switch (action.type) {
    case ActionTypes.SET_USERINFO:
      return action.userInfo;
    default:
      return userInfo;
  }
}
const reducer  = combineReducers({//action
  banners:combineBanners, //banners  mapStateToPropsstore
  userInfo:combineUserInfo

})
export default reducer