// 
import React from 'react';

// import { Link } from 'react-router-dom'
export default class ChildSearch extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:'A',
        value:'xi',
        searchA:'AA'
      }
    }
    inputChange(e){
      // this.setState({
      //   value:e.target.value
      // })
      this.props.searchChange(e.target.value)
    }
    inputChange2(e){
      this.setState({
        searchA:e.target.value
      })
      this.props.searchChange2(e.target.value)
    }
    render() {
        return (
            <div className="ChildSearch">
                  {this.state.title}
                  <div>
                      A
                      1. <input type="text" value={this.props.searchValue} onChange={this.inputChange.bind(this)}/>
                      2. <input type="text" value={this.state.searchA} onChange={this.inputChange2.bind(this)}/>
                  </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
    }
}