// 
import React from 'react';
import MarkDown from '../../components/markdown/markdown-edit'
var cs=require("classnames")
const content=`
[github][https://github.com/JedWatson/classnames]
\`\`\` js
//
1.npm install classnames
2.var cs=require("classnames")
//
//<h2 className={cs('name1','name2')}>classnames</h2>
//<h2 className={cs('name1',{'name2':true})}>classnames</h2>

classNames('foo', 'bar'); // => 'foo bar'
classNames('foo', { bar: true }); // => 'foo bar'
classNames({ 'foo-bar': true }); // => 'foo-bar'
classNames({ 'foo-bar': false }); // => ''
classNames({ foo: true }, { bar: true }); // => 'foo bar'
classNames({ foo: true, bar: true }); // => 'foo bar'
classNames('foo', { bar: true, duck: false }, 'baz', { quux: true }); // => 'foo bar baz quux'
classNames(null, false, 'bar', undefined, 0, 1, { baz: null }, ''); // => 'bar 1'

var arr = ['b', { c: true, d: false }];
classNames('a', arr); // => 'a b c'
\`\`\`
`
export default class PClassnames extends React.Component {
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false       
          },
          markContent:content
        }
        return (
            <div>
                <h2 className={cs('h2')}>classnames</h2>
                <div>
                   <MarkDown {...markConfig}/>
                </div>
            </div>
        )
    }
}