// 
import React from 'react';

import { Link } from 'react-router-dom'
export default class ChangeLine extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:'react',
        html1:'dangerouslySetInnerHTML'
      }
    }
    render() {
        function changeLine(){
            let str =`dangerouslySetInnerHTMLhtml: <br/> function(){ var str =' ' return {__html: str}}`
            return {__html: str}
        }
        return (
            <div className="ChangeLine App_main">
                <div className="common_header">
                  <Link
                    className="App-link"
                    to="/react-basic"
                    style={{ position: "absolute", left: 0, top: "10px" }}
                  >
                    {" "}
                    &lt;--
                  </Link>
                   <div className="common_title">{this.state.title}</div>
                </div>
                <div className="common_content">
                     <div className='common_code_style' style={{border:'1px solid #c4c4c4',paddingBottom:'20px',minHeight:'300px'}}>
                          <div>dangerouslySetInnerHTML</div>
                          <div dangerouslySetInnerHTML={changeLine()}/>
                          <div>css {`whiteSpace:'pre'`}</div>
                           <div style={{whiteSpace:'pre'}}> 
                             {
                               `dangerouslySetInnerHTMLhtml: <br/> function(){ var str =' ' return {__html: str}}`
                             }
                           </div>
                           
                     </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
    }
}