export default class Pfont extends React.Component {
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false,
            height:`100%`           
          },
          markContent:content
        }
        return (
            <div>
                <h2>font</h2>
                <div>
                   <MarkDown {...markConfig}/>
                </div>
            </div>
        )
    }
}