import { connect } from 'react-redux' 
import { setBanners } from '@service/store/action' //store/action.js

class ReactReduxDemo2 extends React.Component {
  componentDidMount(){
     this.props.getBannersList(['a','b','c'])
  }
  render() {
      // console.log('this.props',this.props)
      return (
          <div>              
              <div style={{height:'467px'}}>
                 <h2 className="h2_common">Redux-</h2>
              </div>
          </div>
      )
  }
}

const mapStateToProps = (state) => {
  // console.log('state',state) //redux store
  return {
    banners: state.banners
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getBannersList: (banners) => dispatch(setBanners(banners)) //acion,state
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(ReactReduxDemo2)
